
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { STANDARD_DATE_FORMAT } from "@/utils/Date";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { IonPage } from "@ionic/vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { PatientLabResultService } from "@/services/patient_lab_result_service"
import Validation from "@/components/Forms/validations/StandardValidations"
import { toDate } from "@/utils/Strs";
import { toastWarning } from "@/utils/Alerts";
import { find } from "lodash";
import dayjs from "dayjs";


export default defineComponent({
	components: { IonPage },
	mixins: [EncounterMixinVue],
	data: () => ({
		lab: {} as PatientLabResultService,
		orders: [] as any[]
	}),
	watch: {
		ready: {
			async handler(ready: boolean) {
				if (ready) {
					this.lab = new PatientLabResultService(this.patientID)
					this.orders = await this.lab.getTestsWithoutResults()
					this.fields = [
						this.ordersWithoutResultsField(),
						this.alphaResultField(),
						this.resultField(),
						this.resultDateField()
					];
				}
			},
			immediate: true
		}
	},
	methods: {
		async onFinish(f: any, c: any) {
			this.lab.setTestID(c.result.testID)
			this.lab.setResultDate(f.result_date.label)
			await this.lab.createEncounter()
			await this.lab.createLabResult([c.result.measures])
			this.nextTask();
		},
		ordersWithoutResultsField(): Field {
			let orders: any[] = []
			return {
				id: 'orders',
				helpText: "Select DBS Order",
				type: FieldType.TT_SELECT,
				init: async () => {
					orders.filter((r: any) => /dbs/i.test(r.specimen.name) && r.tests.some(
						(t: any) => /viral load|HIV DNA polymerase chain reaction/i.test(t.name) && t.result === null)
						)
					return true
				},
				isRequired: () => true,
				options: (f: any) => {
					if (f.orders != null) return f.orders
					return this.orders.map((t: any) => {
						const test = find(t.tests, (t: any) => /viral load|HIV DNA polymerase chain reaction/i.test(t.name))
						return {
							label: `#${t.accession_number} ${toDate(t.order_date)}`,
							value: test.concept_id,
							other: { 
								testID: test.id
							}
						}
					})
				}
			}
		},
		resultDateField(): Field {
			return {
				id: 'result_date',
				helpText: "Result date",
				type: FieldType.TT_FULL_DATE,
				isRequired: () => true,
				condition: (f: any) => f.orders.value,
				validation: (v: Option, f: any) => {
					const orderDate = dayjs(f.orders.other.order_date).format(STANDARD_DATE_FORMAT)
					if (new Date(v.label) > new Date(orderDate)) {
						return [`Result date ${v.label} cannot be greater than order date of ${orderDate}`]
					}
					return null
				}
			}
		},
		alphaResultField(): Field{
			return {
				id: `result`,
				helpText: `Select Test Result`,
				type: FieldType.TT_SELECT,
				group: 'test_indicator',
				finalComputedValue: (v: Option, f: any) => {
					return {
						testID: f.orders.other.testID,
						measures: {
							indicator: {
								concept_id: f.orders.value
							},
							value: v.value,
							value_type: 'text'
						}
					}
				},
				appearInSummary: () => false,
				validation: (v: Option) => Validation.required(v),
				condition: async () => this.orders.filter((r: any) => /dbs/i.test(r.specimen.name) && r.tests.some(
						(t: any) => /HIV DNA polymerase chain reaction/i.test(t.name) && t.result === null)
						).length > 0,
				options: () => 
					[
						{   
							label: 'Positive',
							value: 'positive'
						},
						{
							label: 'Negative',
							value: 'negative'
						}
                ]
			}
		},
		resultField(): Field {
			return {
				id: 'result',
				helpText: "Test result",
				type: FieldType.TT_TEXT,
				isRequired: () => true,
				condition: () => this.orders.filter((r: any) => /dbs/i.test(r.specimen.name) && r.tests.some(
						(t: any) => /viral load/i.test(t.name) && t.result === null)
						).length > 0,
				config: {
					customKeyboard: [
						[
							['1', '2', '3'],
							['4', '5', '6', '=', '<', '>'],
							['7', '8', '9', '.'],
							['',  '0', '']
						],
						[
							[ 'Delete']
						]
					]
				},
				onValue: (v: Option) => {
					if (!/^(=|<|>)([0-9]*)$/m.test(`${v.value}`)) {
						toastWarning('You must enter a modifer and numbers only. i.e =90 / >19 / < 750')
						return false
					}
					return true
				},
				finalComputedValue: (v: Option, f: any) => {
					return {
						testID: f.orders.other.testID,
						measures: {
							indicator: {
								concept_id: f.orders.value
							},
							value: parseInt(`${v.value}`.substring(1)),
							value_modifier: `${v.value}`.charAt(0),
							value_type: 'numeric'
						}
					}
				}
			}
		}
	},
});
